import { Helmet } from "react-helmet";
import React, { Component } from 'react';
import { TAGGSHOP_FONT_URL} from "../../../constants"

export default class PopUpStyles extends Component {
  render() {
      const {item, networkData, wall} = this.props;
      const  ugcSetting = item.ugc_personalizaion.UgcSetting;  const isUgcSetting = ugcSetting && Object.keys(ugcSetting).length > 0

    return  item.ugc_personalizaion && ugcSetting && <Helmet>
        {isUgcSetting  && ugcSetting.title_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ugcSetting.title_link_font}.css`} rel="stylesheet" /> : null}
        {isUgcSetting && ugcSetting.price_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ugcSetting.price_link_font}.css`} rel="stylesheet" /> : null}
        {isUgcSetting && ugcSetting.feature_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ugcSetting.feature_link_font}.css`} rel="stylesheet" /> : null}
        {isUgcSetting  && ugcSetting.discount_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ugcSetting.discount_link_font}.css`} rel="stylesheet" />:null}
        {isUgcSetting && ugcSetting.feature_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ugcSetting.feature_link_font}.css`} rel="stylesheet" /> : null}
        {isUgcSetting && ugcSetting.button_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ugcSetting.button_link_font}.css`} rel="stylesheet" /> : null}

        <style>{`
        .tb_pro_title_user {font-family: ${ugcSetting.feature_css_font}; font-size: ${ugcSetting.feature_font_size}px; color: ${ugcSetting.feature_text_color}}
        .tb_author_avatar0012{background: ${networkData.color}!important}
        .ugc-product-title{ font-family: ${ugcSetting.title_css_font}!important; color: ${ugcSetting.product_title_color}!important; font-size:${ugcSetting.product_title_font_size}px!important}
        .tb_pro_discount_price{ font-family: ${ugcSetting.discount_css_font}!important; color: ${ugcSetting.discount_price_color}!important; font-size:${ugcSetting.discount_price_font_size}px!important}
        .tb_pro_price{ font-family: ${ugcSetting.price_css_font}!important; color: ${ugcSetting.original_price_color}!important; font-size:${ugcSetting.original_price_font_size}px!important}
        .ugc-product-link a{ font-family: ${ugcSetting.button_css_font}!important; color: ${ugcSetting.buy_now_color}!important; font-size:${ugcSetting.buy_font_size}px!important; background:${ugcSetting.buy_now_background}!important;}
        `}
         

        </style>

            </Helmet>;
  }
}
