import axios from "axios"
import { ADD_TO_CART_TRACKING, CLIENT_HOST } from "./api"
import HttpClient from "./http-client"

export const GET_SHOPIFY_PRODUCT_DETAILS = (productName) => {
    return axios.get(`${productName}.js`)
}
export const SHOPIFY_ADD_TO_CART = (payload) => {
    return axios.post(`${CLIENT_HOST}/cart/add.js`, payload)
}

export const SHOPIFY_CART_COUNT_ITEM = () => {
    return axios.get(`${CLIENT_HOST}/cart.js`)
}

export const TRACK_ADD_TO_CART = (obj) => {
    axios.post(ADD_TO_CART_TRACKING,obj).then(res => {}).catch(error => console.log("error",error))
}