import React, { Component } from 'react';
import { connect } from 'react-redux';
import { showHotspot, themePostTracking } from '../../../../actions/themeActions';
import ProductImg from './Media/productImg'
import ReactHtmlParser from 'react-html-parser'; 

const URL = `https://app.taggbox.com/commerce/`;

class PopUpMedia extends Component {
    state={prdImgLoaded:false, noImg:false}

    openUrl = (url) => {
        const { item, wall} = this.props;
        const UgcSetting = item.ugc_personalizaion.UgcSetting;
        var newUrl = url;
        if(wall?.User?.id == 114259){
           newUrl +=`?utm_source=tagshop&utm_medium=gallery&utm_campaign=${item.network.name}`
        }else{
            if(wall?.Wall.utm_status==1) newUrl +=`?utm_source=${wall?.Wall.utm_source}&utm_medium=${wall?.Wall.utm_medium}&utm_campaign=${wall?.Wall.utm_campaign}`
        }

        if(parseInt(UgcSetting.tab_target) == 2) window.parent.location.href = newUrl
        else window.open(newUrl, '_blank');
      }
    tracking=( wall, product_id)=>{ 
        themePostTracking({ type: 1, action: 2, wall: wall?.Wall?.id, product_id: product_id , post:this.props.item.referenceId})
    }
      
    render() {
        const { item, ugc_products, wall, showHotspot, openAddtocart, productLoading, showAddToCart, onsetProductImg } = this.props;
        const { prdImgLoaded, noImg } = this.state;
        const ugcSetting = item.ugc_personalizaion.UgcSetting;
        const ownerId = this.props.appData.wall.User.id;

        return <>


            <div className="tb_shop_cont">
                <div className={`tb_pro_title tb_pro_title_user tb-cTSfont-${ugcSetting.feature_font_varient}`}>{ReactHtmlParser(ugcSetting.feature_text)}</div>
                <div className="ts_modal_product tb_modal_pro">
                    {ugc_products.map((product, index) => <div className="tb_pro_item" key={index}>

                        <div className={`tb_pro_wrapp product-item-${index}`} onMouseEnter={()=> showHotspot({showHotspot:true, index, hotspotId: product.id})} onMouseLeave={() => showHotspot({showHotspot:false, index, hotspotId: product.id})}>
                         <ProductImg imgUrl={product.UgcProduct.product_image}/>
                            {(item.ugc_personalizaion && ugcSetting && product.UgcProduct.product_title) ?
                                <div className={`ugc-product-title tb-cTSfont-${ugcSetting.title_font_varient}`}> {product.UgcProduct.product_title}</div> : null}

                            {product.UgcProduct.product_price > 0 || product.UgcProduct.product_discount > 0 ? <div className="ugc-product-price">
                                <div className={`tb_pro_price tb-cTSfont-${ugcSetting.price_font_varient}`}> {product.UgcProduct.price_currency_symbol}{" "}{product.UgcProduct.product_discount > 0 ? product.UgcProduct.product_discount : product.UgcProduct.product_price}</div>
                            </div> : null}
                            {product.UgcProduct.product_discount > 0 &&  product.UgcProduct.product_discount != product.UgcProduct.product_price ?
                                <div className={`tb_pro_discount_price  tb-cTSfont-${ugcSetting.discount_font_varient}`} >{product.UgcProduct.price_currency_symbol}{" "}{product.UgcProduct.product_price}</div> : null}
                            {!productLoading ?
                                showAddToCart ?
                                    <div className={`ugc-product-link ${ownerId == 30656 ? 'homepage-ig-product-link' : ''}`}><a onClick={() => {openAddtocart(product.id, product); onsetProductImg(product.UgcProduct.product_image); this.tracking( wall, product.UgcProduct.id)}} className={`tb-cTSfont-${ugcSetting.button_font_varient}`} >{ugcSetting.buy_now_text}</a></div>
                                    :
                                    <div className={`ugc-product-link ${ownerId == 30656 ? 'homepage-ig-product-link' : ''}`}><a onClick={()=>{this.openUrl(product.UgcProduct.product_url); this.tracking( wall, product.UgcProduct.id)}} className={`tb-cTSfont-${ugcSetting.button_font_varient}`} target={wall && wall.User && wall.User.id != `102810` ? "_blank" : ''}>{ugcSetting.buy_now_text}</a></div>
                                :
                                <></>}
                            {/* <div onClick={() => openAddtocart(product.id)}>Add to Cart</div> */}
                        </div>
                    </div>
                    )}</div>
            </div>
        </>
    }
}


const mapStateToProps = state => ({
    appData: state.appData
  })
  const mapDispatchToProps = dispatch => {
    return {
     
     showHotspot: (data) => dispatch(showHotspot(data))
    }
  }

export default connect(mapStateToProps, mapDispatchToProps)(PopUpMedia);
