import React, { Component } from 'react'
import { SHOPIFY_ADD_TO_CART, TRACK_ADD_TO_CART } from '../../../../../actions/addToCartAction'
import { NO_CAMERA_IMAGE, NO_PRODUCT_IMAGE } from '../../../../../constants'
import { source } from '../../../../../actions/http-client'
// import Button from 'react-bootstrap/Button';
// import Modal from 'react-bootstrap/Modal';

export default class addToCart extends Component {
    state = { allSizes: null, allColors: null, selectedColor: null, selectedSize: null, availableSize: [], productImage: '', optionError: false }
    componentDidMount() {
        const { productDetails } = this.props
        this.getOptions()
        this.getProductImage()
    }
    getOptions = () => {
        const { productDetails } = this.props
        var colors = []
        var size = []
        if (productDetails && productDetails.options && productDetails.options.length) {
            productDetails.options.map(option => {
                if (option.values && option.values.length) {
                    // options[option.name] = option.values
                    if (option.name.toLowerCase() == 'color') { colors = option.values; this.setState({ selectedColor: option.values[0] }) }
                    if (option.name.toLowerCase() == 'size') size = option.values
                    else { colors = option.values; this.setState({ selectedColor: option.values[0] }) }
                }
            })
        }
        this.setState({ allSizes: size, allColors: colors })
        if (!colors || !colors.length) {
            var availableSize = []
            productDetails.variants && productDetails.variants.length && productDetails.variants.map(vr => {
                if (vr.available) {
                    if (!availableSize.includes(vr.option1)) availableSize.push(vr.option1)
                }
            })
            this.setState({ availableSize })

        }
        else if(size && size.length) this.checkAvablity(colors[0])
    }

    checkAvablity = color => {
        const { variants } = this.props.productDetails
        this.getProductImage()
        var availableSize = []
        variants.map(vr => {
            if (vr.option1.toLowerCase() == color.toLowerCase() && vr.available) {
                if (!availableSize.includes(vr.option2)) availableSize.push(vr.option2)
            }
            if (vr.option2?.toLowerCase() == color.toLowerCase() && vr.available) {
                if (!availableSize.includes(vr.option1)) availableSize.push(vr.option1)
            }
        })
        this.setState({ availableSize })

    }

    onAddToCart = () => {
        console.log("addtocart")
        try {
            const { variants } = this.props.productDetails
            const { selectedColor, selectedSize, allColors, allSizes } = this.state
            if ((!selectedSize || selectedSize == '') && (allSizes && allSizes.length)) {
                this.setState({ optionError: true })
                console.log("optionError")
                return
            }
            if (variants.length == 1) {
                console.log("variants.length == 1")
                this.props.onSelectedVariant(variants[0])
                this.addProductsToCart(variants[0])
            }
            else {
                console.log("else")

                variants.map(vr => {
                    if (!selectedColor || !selectedColor.length) {
                        if (vr.options.includes(selectedSize)) {
                            this.props.onSelectedVariant(vr)
                            this.addProductsToCart(vr)
                        }
                    }
                    else {
                        if (vr.options.includes(selectedColor) && vr.options.includes(selectedSize)) {
                            this.props.onSelectedVariant(vr)
                            this.addProductsToCart(vr)
                        }
                        else if(vr.options.includes(selectedColor)) {
                            this.props.onSelectedVariant(vr)
                            this.addProductsToCart(vr)
                        }
                    }
                })
            }
        } catch (error) {
            console.log('error', error)
        }
    }

    addProductsToCart = (variant) => {
        const { wall, productDetails,tagshopProductDeatil } = this.props
        const payload = new FormData()
        payload.append('id', variant.id)
        payload.append('quantity', 1)
        SHOPIFY_ADD_TO_CART(payload).then(res => {
            const obj = {
                variant: variant.options ? variant.options.join(",") : "",
                price: variant.price / 100,
                wall: source == 'website' ? wall.Wall.wallId : wall.Wall.id,
                website_id: source == 'website' ? wall.Wall.id : 0,
                product_id: tagshopProductDeatil.ugc_product,
                quantity:1,
                post: tagshopProductDeatil.post_id,
               }
               TRACK_ADD_TO_CART(obj)
            this.props.onAddToCart()
        }).catch(err => {
            // const obj = {
            //     variant: variant.options ? variant.options.join(",") : "",
            //     price: variant.price / 100,
            //     wall: wall.Wall.wallId,
            //     website_id: wall.Wall.wallId,
            //     product_id: tagshopProductDeatil.id,
            //     quantity:1
               
            //    }
            //    TRACK_ADD_TO_CART(obj)
            this.props.onAddToCart()
        })
    }

    getColorImage = (color) => {
        const { productDetails } = this.props
        if(color){
            const result = productDetails.variants.find(vr => vr.options.includes(color))
            // this.setState({ productImage: result.featured_image ? result.featured_image?.src : this.props.productImg})
            return result.featured_image ? result.featured_image?.src : this.props.productImg
        }
    }

    getProductImage = () => {
        const { productDetails } = this.props
        const { selectedColor } = this.state
        if (selectedColor) {
            // const result = productDetails.variants.find(vr => {
            //     if (vr.options.includes(selectedColor)) {
            //         // this.props.onSelectedVariant(vr)
            //         // this.addProductsToCart(vr.id)
            //         // return vr.featured_image ? vr.featured_image?.src : false
            //         this.setState({ productImage: vr.featured_image ? vr.featured_image?.src : "" })
            //         // return false
            //     }
            // })
            const result = productDetails.variants.find(vr => vr.options.includes(selectedColor))
            this.setState({ productImage: result.featured_image ? result.featured_image?.src : this.props.productImg})
        }
        else { this.setState({ productImage: productDetails.featured_image }) }
    }


    render() {
        const { closeAddtocart, addtoCartPopup, productDetails, onSelectedVariant , productImg, tagshopProductDeatil} = this.props
        const { allColors, allSizes, selectedColor, availableSize, selectedSize, productImage, optionError } = this.state
        return (
            <div className="ts_addToCart_modal" aria-hidden="true">
                <div className="ts_addToCart_modalDialog" role="document">
                    <div className="ts_addToCart_modalContent">
                        <div className="ts_addToCart_modalBody">
                            <div className="ts_addToCart_backButton" onClick={closeAddtocart}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="13" height="10" viewBox="0 0 13 10" fill="none">
                                    <path d="M12.3496 5.125C12.3496 5.58203 11.9941 5.9375 11.5625 5.9375H3.76758L6.43359 8.62891C6.76367 8.93359 6.76367 9.4668 6.43359 9.77148C6.28125 9.92383 6.07812 10 5.875 10C5.64648 10 5.44336 9.92383 5.29102 9.77148L1.22852 5.70898C0.898438 5.4043 0.898438 4.87109 1.22852 4.56641L5.29102 0.503906C5.5957 0.173828 6.12891 0.173828 6.43359 0.503906C6.76367 0.808594 6.76367 1.3418 6.43359 1.64648L3.76758 4.3125H11.5625C11.9941 4.3125 12.3496 4.69336 12.3496 5.125Z" fill="#6D7175" />
                                </svg>
                                Back
                            </div>
                            <div className="ts_addToCart_modalBody_inner">
                                <div className="ts_addToCart_productImage">
                                    <img src={productImage ? productImage : NO_PRODUCT_IMAGE} alt="product img" onError={e => this.setState({productImage: NO_PRODUCT_IMAGE})} />
                                </div>
                                <div className="ts_addToCart_productDetail">
                                    <div className="ts_addToCart_productTitle">{productDetails.title}</div>
                                    <div className="ts_addToCart_productPrice">{tagshopProductDeatil?.UgcProduct?.price_currency_symbol}{tagshopProductDeatil?.UgcProduct.product_discount>0?tagshopProductDeatil?.UgcProduct.product_discount: tagshopProductDeatil?.UgcProduct.product_price}</div>
                                    {optionError ? <div className="ts_addToCart_selectOptionsError">Please select any one variant to proceed!</div> : <></>}
                                    <div className="ts_addToCart_colorPalate_section">
                                        {allColors && allColors.length ?
                                            <>
                                                <div className="ts_addToCart_colorHeading">
                                                    Color:
                                                </div>
                                                <div className="ts_addToCart_colorPalate">
                                                    {allColors.map(color => (
                                                        <div className={`ts_addToCart_color ${selectedColor == color ? 'active' : ''}`}>
                                                            <div className="ts_addToCart_colorTile" style={{ backgroundColor: color }} onClick={() => this.setState({ selectedColor: color }, () => this.checkAvablity(color))}><img src={this.getColorImage(color)} style={{height: '100%', width: '100%'}} onError={e => e.target.src = NO_PRODUCT_IMAGE} /></div>
                                                        </div>
                                                    ))}
                                                </div>
                                            </>
                                            : <></>}
                                    </div>
                                    {allSizes && allSizes.length ? <div className="ts_addToCart_sizeChart">
                                        <div className="ts_addToCart_sizeChart_label">
                                            Size
                                        </div>
                                        {allSizes.map(size => (
                                            availableSize.includes(size) ?
                                                <div className={`ts_addToCart_sizebutton  ${size == selectedSize ? 'active' : ''}`} onClick={() => this.setState({ selectedSize: size, optionError: false })}>
                                                    {size}
                                                </div>
                                                : <div className="ts_addToCart_sizebutton ts_productSize_unvavailable">
                                                    <span className="ts_productSize_unvavailable_cross"></span>
                                                    {size}
                                                </div>
                                        ))}
                                    </div>
                                        : <></>}
                                    <div className="ts_addToCart_actions">
                                        <div className="ts_addToCart_actions_cancel" onClick={closeAddtocart}>Cancel</div>
                                        <div className="ts_addToCart_actions_addCart" onClick={this.onAddToCart}>ADD TO CART</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}
