import React, { Component } from 'react'
import { SHOPIFY_CART_COUNT_ITEM } from '../../../../../actions/addToCartAction'
import { CLIENT_HOST } from '../../../../../actions/api'
import { NO_PRODUCT_IMAGE } from '../../../../../constants'

export default class continueShopping extends Component {
  state = { cartItems:1}

  componentDidMount(){
    SHOPIFY_CART_COUNT_ITEM().then(res => {
      const {item_count} = res.data 
      if(item_count)  this.setState({cartItems:item_count})
  }).catch(err => {
      
  })
  }

  render() {
    const { productDetails, closeAddtocart, productImg, tagshopProductDeatil } = this.props 
    return (
      <div className="ts_addToCart_modal ts_checkout_modal" aria-hidden="true">
        <div className="ts_addToCart_modalDialog" role="document">
            <div className="ts_addToCart_modalContent">
                <div className="ts_addToCart_modalBody">
                    <div className="ts_addToCart_backButton" onClick={closeAddtocart}>
                      <svg xmlns="http://www.w3.org/2000/svg" width="13" height="10" viewBox="0 0 13 10" fill="none">
                        <path d="M12.3496 5.125C12.3496 5.58203 11.9941 5.9375 11.5625 5.9375H3.76758L6.43359 8.62891C6.76367 8.93359 6.76367 9.4668 6.43359 9.77148C6.28125 9.92383 6.07812 10 5.875 10C5.64648 10 5.44336 9.92383 5.29102 9.77148L1.22852 5.70898C0.898438 5.4043 0.898438 4.87109 1.22852 4.56641L5.29102 0.503906C5.5957 0.173828 6.12891 0.173828 6.43359 0.503906C6.76367 0.808594 6.76367 1.3418 6.43359 1.64648L3.76758 4.3125H11.5625C11.9941 4.3125 12.3496 4.69336 12.3496 5.125Z" fill="#6D7175"/>
                      </svg>
                      Back
                    </div>
                    <div className="ts_addToCart_modalBody_inner">
                      <div className="ts_addToCart_productImage">
                          <img src={productDetails.featured_image ? productDetails.featured_image.src : productImg} alt="product img" onError={e => e.target.src = NO_PRODUCT_IMAGE} />
                      </div>
                      <div className="ts_addToCart_productDetail">
                          <div className="ts_addToCart_productTitle">{productDetails.name}</div>
                          <div className="ts_addToCart_productPrice">{tagshopProductDeatil?.UgcProduct?.price_currency_symbol}{tagshopProductDeatil?.UgcProduct.product_discount>0?tagshopProductDeatil?.UgcProduct.product_discount: tagshopProductDeatil?.UgcProduct.product_price}</div>
                          {this.state.cartItems ? <div className="ts_addToCart_product_added">
                            Your cart now has {this.state.cartItems} item
                          </div> : <></>}
                          <div className="ts_addToCart_actions">
                              <div className="ts_addToCart_actions_checkOut" onClick={() => window.location = `${CLIENT_HOST}/cart`}>Proceed to checkout</div>
                              <div className="ts_addToCart_actions_shopping" onClick={closeAddtocart}>Continue Shopping</div>
                          </div>
                      </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    )
  }
}
